$mozBack: -moz-radial-gradient(0 100%, circle, rgba(204,0,0,0) 5px, #f1ac11 5px),
          -moz-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 5px, #f1ac11 5px),
          -moz-radial-gradient(100% 0, circle, rgba(204,0,0,0) 5px, #f1ac11 5px),
          -moz-radial-gradient(0 0, circle, rgba(204,0,0,0) 5px, #f1ac11 5px);
$oBack: -o-radial-gradient(0 100%, circle, rgba(204,0,0,0) 5px, #f1ac11 5px),
        -o-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 5px, #f1ac11 5px),
        -o-radial-gradient(100% 0, circle, rgba(204,0,0,0) 5px, #f1ac11 5px),
        -o-radial-gradient(0 0, circle, rgba(204,0,0,0) 5px, #f1ac11 5px);
$wBack: -webkit-radial-gradient(0 100%, circle, rgba(204,0,0,0) 5px, #f1ac11 5px),
        -webkit-radial-gradient(100% 100%, circle, rgba(204,0,0,0) 5px, #f1ac11 5px),
        -webkit-radial-gradient(100% 0, circle, rgba(204,0,0,0) 5px, #f1ac11 5px),
        -webkit-radial-gradient(0 0, circle, rgba(204,0,0,0) 5px, #f1ac11 5px);
$yellow: #f1ac11;

//VARIABLES ^^^

section.menu {
  width: 100%;
  position: relative;
  background-color: #fff;

  h1 {
    font-size: 2.9rem;
    color: $yellow;
    margin-bottom: 50px;
    text-align: center;
    text-transform: uppercase;
    margin-top: 80px;
    font-style: italic;
    font-weight: 400;
  }
  .foods {
    width: 900px;
    position: relative;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    .food {
      width: 430px;
      height: 80px;
      position: relative;
      margin-bottom: 40px;

      img {
        width: 80px;
        height: 80px;
        display: block;
        position: absolute;
        left: 0;
        top: 0;
      }

      .food-description {
        width: 70%;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;

        .description-top {
          width: 100%;
          height: 50%;
          position: absolute;
          top: 0;
          left: 0;
          overflow: hidden;
          &::after {
            content: "";
            width: 80%;
            position: absolute;
            right: 50px;
            bottom: 6px;
            border: 3px dotted #000;
            border-left: none;
            border-right: none;
            border-top: none;
          }

          .description-top-p {
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            font-size: 1.2rem;
            text-transform: uppercase;
            background-color: #fff;
            padding-right: 3px;
            z-index: 222;
          }
          .price-cart {
            width: 45px;
            height: 27px;
            background: $yellow;
            background: $mozBack;
            background: $oBack;
            background: $wBack;
            background-position: bottom left, bottom right, top right, top left;
            -moz-background-size: 50% 50%;
            -webkit-background-size: 50% 50%;
            background-size: 50% 50%;
            background-repeat: no-repeat;
            position: absolute;
            bottom: 0;
            right: 0;
            z-index: 222;
            &::after {
              content: "";
              width: 100%;
              height: 2px;
              position: absolute;
              background-color: $yellow;
              left: 0;
              top: 50%;
              margin-top: -1px;
              z-index: -1;
            }
            &::before {
              content: "";
              width: 2px;
              height: 100%;
              position: absolute;
              background-color: $yellow;
              left: 50%;
              top: 0;
              margin-left: -1px;
              z-index: -1;
            }

            p {
              font-size: 1.2rem;
              font-weight: bold;
              text-align: center;
              line-height: 27px;
            }
          }
        }
        .description-bottom {
          width: 100%;
          height: 50%;
          position: absolute;
          bottom: 0;
          left: 0;
          &::after {
            content: "";
            width: 100%;
            position: absolute;
            left: 0;
            bottom: 6px;
            border: 3px dotted grey;
            border-left: none;
            border-right: none;
            border-top: none;
          }

          p {
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            font-size: 1rem;
            color: grey;
            text-transform: uppercase;
            background-color: #fff;
            z-index: 222;
          }
          span {
            display: block;
            position: absolute;
            right: 0;
            bottom: 0;
            font-size: 1rem;
            color: grey;
            background-color: #fff;
            z-index: 222;
          }
        }
      }
    }
  }
  .online-menu {
    width: 900px;
    height: 300px;
    margin: 99px auto 80px auto;
    position: relative;
    background: #fff;
    box-shadow:
      0 25px 12px -25px #000,
      0 -25px 12px -25px #000,
      25px 0 12px -25px #000,
      -25px 0 12px -25px #000;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    &::after {
      content: "";
      width: 50px;
      height: 50px;
      position: absolute;
      bottom: -25px;
      left: -25px;
      background-color: #fff;
      border-radius: 50%;
      z-index: 444;
      box-shadow: inset 0 0 13px -1px #000;
    }
    &::before {
      content: "";
      width: 50px;
      height: 50px;
      position: absolute;
      top: -25px;
      left: -25px;
      background-color: #fff;
      border-radius: 50%;
      z-index: 444;
      box-shadow: inset 0 0 13px -1px #000;
    }
    span.online-menu-topcorner {
      display: block;
      width: 50px;
      height: 50px;
      position: absolute;
      top: -25px;
      right: -25px;
      background-color: #fff;
      border-radius: 50%;
      z-index: 444;
      box-shadow: inset 0 0 13px -1px #000;
    }
    span.online-menu-bottomcorner {
      display: block;
      width: 50px;
      height: 50px;
      position: absolute;
      bottom: -25px;
      right: -25px;
      background-color: #fff;
      border-radius: 50%;
      z-index: 444;
      box-shadow: inset 0 0 13px -1px #000;
    }

    .online-menu-left {
      width: 50%;
      height: 100%;
      background-image: url("../images/spagetty.jpg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
    .online-menu-right {
      width: 50%;
      height: 100%;
      position: relative;
      padding: 30px;

      h3 {
        font-size: 0.8rem;
        text-transform: uppercase;
        color: $yellow;
        font-style: italic;
        margin-bottom: 20px;
        font-weight: 400;
      }
      h2 {
        font-size: 1.7rem;
        text-transform: uppercase;
        color: #000;
        font-style: italic;
        margin-bottom: 20px;
        font-weight: 400;
      }
      p {
        font-size: 0.8rem;
        font-style: italic;
        color: #d4d4d4;
        margin-bottom: 24px;
        font-weight: bold;
      }
      button.shaded-btn {
        position: relative;
        transition: 0.2s;
        font-size: 1rem;
        padding: 10px 12px 9px 13px;
        border: none;
        background-color: $yellow;
        font-style: italic;
        border-radius: 2px;
        box-shadow: 0 3px 0 0 #b47d00;

        &:hover {
          cursor: pointer;
        }
        &:focus {
          margin-top: 3px;
          box-shadow: none;
          border: none;
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  section.menu {
    .foods {
      width: 100%;
      justify-content: center;
      .food {

      }
    }
    .online-menu {
      width: 400px;
      height: auto;

      .online-menu-left, .online-menu-right {
        width: 100%;
        height: 300px;
      }
    }
  }
}
@media screen and (max-width: 450px) {
  section.menu {
    .foods {
      .food {
        width: 300px;

        img {
          width: 60px;
          height: 60px;
          top: 50px;
          margin-top: -30px;
        }

        .food-description {
          width: 77%;
          .description-top {
            p.description-top-p {
              font-size: 0.8rem;
              font-weight: bold;
            }
          }
          .description-bottom {
            p, span {
              font-size: 0.8rem;
            }
          }
        }
      }
    }

    .online-menu {
      width: 300px;
      height: auto;

      .online-menu-right {
        h3 {
          margin-bottom: 15px;
        }
        h2 {
          font-size: 1.2rem;
          margin-bottom: 15px;
        }
        p {
          margin-bottom: 15px;
        }
      }
    }
  }
}
